// Timer Worker
// Runs a loop efficiently off the main thread to avoid background throttling

const intervalIds: Map<number, ReturnType<typeof setInterval>> = new Map();

self.onmessage = (e: MessageEvent) => {
    const { type, id, interval } = e.data;

    if (type === 'start') {
        if (intervalIds.has(id)) {
            clearInterval(intervalIds.get(id)!);
        }

        const timerId = setInterval(() => {
            self.postMessage({ type: 'tick', id });
        }, interval);

        intervalIds.set(id, timerId);
    } else if (type === 'stop') {
        if (intervalIds.has(id)) {
            clearInterval(intervalIds.get(id)!);
            intervalIds.delete(id);
        }
    }
};
